/*
Tic tac toe
*/

body {
    padding: 20px;
}

h1 {
    font-size: 175%;
    margin-bottom: 0.5em;
}

p {
    margin-bottom: 1em;
}

#evaluated {
    color: black;
}
#xo{
    position: absolute;
    flex-direction: row;
    display: flex;
    margin-left: 49%;
    transform: translateX(-53%);
    top: 45px;
    text-align: center;
}

#x{
color: white;
height: 30px;
width: 200px;
font-size: 25px;
border-left: rgb(26, 26, 29);
border: 1px solid black;
text-align: center;
font-family: Arial, Helvetica, sans-serif;
font-weight: 500;

border-bottom-width: 10px;
background-color:rgb(29, 28, 20) ;
}
#turn{
position: absolute;
margin-left: 49%;
transform: translateX(-53%);
top: 25px;
}
#o{
border-right: rgb(26, 26, 29);
color: white;
height: 30px;
width: 200px;
font-size: 25px;
border: 1px solid black;
text-align: center;
font-family: Arial, Helvetica, sans-serif;
font-weight: 500;
background-color:rgb(29, 28, 20) ;
}
.board  {
margin-top: 5%;
transform: translateY(20%);
}

.board td {
    height: 120px;
    text-align: center;
    vertical-align: middle;
    width: 120px;
}

.board td.winner {
    color: #00CC00;
}

.board td.best {
    background-color: #F5F5F5;
}

.board #one,
.board #two {
    border-right: 1px solid white;
    border-bottom: 1px solid white;
}

.board #three,
.board #six {
    border-bottom: 1px solid white;
}

.board #four,
.board #five {
    border-right: 1px solid white;
    border-bottom: 1px solid white;
}

.board #seven,
.board #eight {
    border-right: 1px solid white;
}

.board td.highlight {
    background-color: #F5F5F5;
}

td{
    font-size: 30px;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
}