*{
    background-color: rgb(26, 26, 29);
}
#TwoPlayerModeDIV,#MulPlayerModeDIV,#SinglePlayerModeDIV{
    visibility: hidden;
    display: none;
}
#modeSelector{
text-align: center;
height: 450px;
width: 400px;
background-color:rgb(29, 28, 20) ;
border-radius: 25px;
position: absolute;
top: 50%;
margin-left: 50%;
margin-right: 50%;
transform: translate(-50%,-50%);
}




#multiplayer:active{
    border: 1px solid red;
}
#twoplayer:active{
    border: 1px solid red;
}
#singleplayer:active{
    border: 1px solid red;
}

#multiplayer{
    height: 25%;
    margin-bottom: 3px;
    border-radius: 5px ;
    border: 1px solid black;
    width: 50%;
    background-color:rgb(14, 14, 14) ;
    margin-left: 50%;
    margin-right: 50%;
    transform: translate(-50%,0);
}

#singleplayer{
    height: 25%;
    margin-bottom: 3px;
    border-radius: 5px ;
    border: 1px solid black;
    width: 50%;
    background-color:rgb(14, 14, 14) ;
    margin-left: 50%;
    margin-right: 50%;
    transform: translate(-50%,0);
}
#twoplayer{
    height: 25%;
    margin-bottom: 3px;
    border-radius: 5px ;
    border: 1px solid black;
    width: 50%;
    background-color:rgb(14, 14, 14) ;
    margin-left: 50%;
    margin-right: 50%;
    transform: translate(-50%,0);
}
#winnername{
    background-color:rgb(14, 14, 14) ;
    font-size: 25px;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    padding-bottom: 3px;
    border-bottom: 1px solid #adadad;
}
#menu{
    position: absolute;
    margin-top: 90px;
    left: 50%;
    transform: translateX(-50%);
    width: 90px;
    height: 30px;
}
#replay{
    position: absolute;
    margin-top: 50px;
    left: 50%;
    transform: translateX(-50%);
    width: 90px;
    height: 30px;
}
#onWIN{
    visibility: hidden;
    text-align: center;
    position: absolute;
    z-index: 2000;
    height: 25%;
    margin-bottom: 3px;
    border-radius: 5px ;
    border: 1px solid black;
    width: 200px;
    background-color:rgb(14, 14, 14) ;
    margin-left: 50%;
    margin-right: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
#a,#nd2{
    background-color:rgb(14, 14, 14) ;
    margin-top: -15%;
    margin-left: 15%;
    color: white;
    font-weight: 300;
    font-family: Arial, Helvetica, sans-serif;
}
#nd2{
    margin-left: 23%;
}
img{
height: 40px;
width: 50px;
margin-left: -60%;
border: 1px solid black;
margin-top: 15%;
border-radius: 50px;
}
#s{
    font-size: 32px;
    font-family: Arial, Helvetica, sans-serif;
    background-color:rgb(29, 28, 20) ;
    padding-bottom: 3px;
    border-bottom: 1px solid #adadad;
    border-bottom-width: -0.01px;
    width: 100%;
}